import fs from "node:fs";
import path from "node:path";
import type { AvailableFeatures } from "../types";

/**
 * Check the readme for a list of available features.
 */
export function hasFeature(name: AvailableFeatures): boolean {
  try {
    const availableAPIs = JSON.parse(
      fs.readFileSync(path.join(__dirname, "../../apis.json"), "utf8"),
    );

    return !!availableAPIs?.features?.includes(name);
  } catch (e) {
    console.log(e);
    throw new Error(`Couldn't check available APIs.`);
  }
}
